
@font-face {
	font-family: 'Compacta-BT';
	font-weight: 300;
	font-style: normal;
	src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
}

@font-face {
	font-family: 'Compacta-BT';
	font-weight: 400;
	font-style: normal;
	src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
}

@font-face {
	font-family: 'Proxima Nova';
	font-weight: 400;
	font-style: normal;
	src: url('../fonts/proxima-nova/Proxima-Nova-Reg.eot');
	src: url('../fonts/proxima-nova/Proxima-Nova-Reg.eot?#iefix') format('embedded-opentype'),
	url('../fonts/proxima-nova/Proxima-Nova-Reg.woff') format('woff'), 
	url('../fonts/proxima-nova/Proxima-Nova-Reg.ttf') format('truetype'),
	url('../fonts/proxima-nova/Proxima-Nova-Reg.svg#codropsicons') format('svg');
}

@font-face {
	font-family: 'American-Made-Icons';
	src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
}


.am-icon {
	position: relative;
	top: 1px;
	display: inline-block;
	font-family: 'American-Made-Icons', sans-serif;
	font-style: normal;
	font-weight: 400;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin make-icon($codepoint, $unicode) {
	.am-icon-#{$codepoint}:before {
		content: $unicode;
	}
}

@include make-icon(a1, '\e001');
@include make-icon(m1, '\e002');
@include make-icon(e1, '\e003');
@include make-icon(r1, '\e004');
@include make-icon(i1, '\e005');
@include make-icon(c1, '\e006');
@include make-icon(a2, '\e007');
@include make-icon(n1, '\e008');

@include make-icon(m2, '\e009');
@include make-icon(a3, '\e010');
@include make-icon(d1, '\e011');
@include make-icon(e2, '\e012');

@include make-icon(a1-thin, '\e013');
@include make-icon(m1-thin, '\e014');
@include make-icon(e1-thin, '\e015');
@include make-icon(r1-thin, '\e016');
@include make-icon(i1-thin, '\e017');
@include make-icon(c1-thin, '\e018');
@include make-icon(a2-thin, '\e019');
@include make-icon(n1-thin, '\e020');

@include make-icon(m2-thin, '\e021');
@include make-icon(a3-thin, '\e022');
@include make-icon(d1-thin, '\e023');
@include make-icon(e2-thin, '\e024');

@include make-icon(plane, '\e025');
@include make-icon(plane2, '\e027');
@include make-icon(close, '\e026');
@include make-icon(play, '\e036');
@include make-icon(pause, '\e039');

